<template>
  <div>
    <div class="l_top">
      <el-row>
        <!-- <el-button type="primary" @click="dialogFormVisible = true" style="margin-top:20px;margin-left:50px">增加设备</el-button> -->
        <el-button type="primary" @click="newMember" style="margin-top:20px;margin-left:50px">添加客户</el-button>
      </el-row>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" show-overflow-tooltip></el-table-column>
      <el-table-column label="客户编号" show-overflow-tooltip>
        <template slot-scope="scope">{{ scope.row.mid }}</template>
      </el-table-column>
      <el-table-column prop="name" label="客户姓名"></el-table-column>
      <el-table-column prop="sex" label="客户性别" :formatter="sexFormat"></el-table-column>
      <el-table-column prop="mobile" label="客户手机号" show-overflow-tooltip></el-table-column>
      <el-table-column prop="identify" label="身份证号" show-overflow-tooltip></el-table-column>
      <el-table-column label="操作" width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="editMember(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-size="size"
      layout="total, prev, pager, next, jumper"
      :total="totalElements"
    ></el-pagination>
  </div>
</template>

<script>
// import qs from "qs";
export default {
  data() {
    return {
      page: 1,
      size: 10,
      totalElements: 1,
      tableData3: null
    };
  },

  methods: {
    handleCurrentChange(val) {
      this.page = val;
      this.list();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    editMember(index, row) {
      this.$router.push({
        path: "/member/detail",
        query: { id: row.id }
      });
    },

    // 新增
    newMember() {
      this.$router.push("/member/detail");
    },

    //列表获取
    list() {      
      var that = this
      this.$get("/members", {
          params: {
            page: that.page,
            size: that.size
          } 
        })
        .then(function(res) {
          that.tableData3 = res.data.content;
          that.page = res.data.pageable.pageNumber + 1;
          that.totalElements = res.data.totalElements;
        })
        .catch(function(error) {});
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    handleDelete(index, row) {
      var that = this;
      const id = row.id;
      this.$axios({
        method: "delete",
        url: "/members/" + id
        // headers: { Authorization: sessionStorage.getItem("auth") }
      }).then(function(res) {
        if (res.status == 200) {
          that.$message({
            message: "删除成功",
            type: "success"
          });
        }
      });
    },
    sexFormat(row, column, cellValue, index){
      const sex = row[column.property];
      if(sex == 1){
       return "男";
      }else{
       return "女"
      }
    }
  },
  mounted() {
    this.list();
  }
};
</script>

<style>
.l_top {
  height: 80px;
}
</style>



